.custom-table {
	&.table {
		border-collapse: separate;
		border-spacing: 0 rem(16px);
		th,
		td {
			border: none !important;
		}
		th {
			color: $gray-500;
		}

		tbody {
			tr {
				background-color: white;
				box-shadow: $global-shadow;
				border-radius: $global-border;
				&.job-is-deactivate {
					// pointer-events: none;
					opacity: 0.6;
				}
			}
			td {
				vertical-align: middle;
				padding: rem(16px 0);
				color: $gray-700;
			}
			.job-title {
				// display: flex;
				// align-items: center;
				.is-immidiatly {
					color: white;
					background-color: $danger;
					border-radius: 32px;
					font-size: rem(12px);
					padding: rem(4px 12px);
				}
			}
			.job-is-active {
				display: block;
				margin: auto;
				color: white;
				background-color: $active;
				border-radius: 32px;
				font-size: rem(10px);
				padding: rem(8px 16px);
				width: rem(60px);
				&.is-deactivate {
					background-color: $deactivate;
				}
			}
			.job-category {
				display: inline-block;
				margin: auto;
				color: white;
				background-color: $text-color-blue;
				border-radius: 32px;
				font-size: rem(10px);
				padding: rem(8px 16px);
				min-width: rem(60px);
			}
		}
	}
}
