.job-intro {
	background-position: 20% 30%;
	height: rem(400px);
	display: flex;
	justify-content: center;
	align-items: flex-start;
	flex-direction: column;
	text-align: right;
	direction: rtl;

	&::after {
		content: '';
		position: absolute;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: $more-info-overlay;
		z-index: 1;
	}
	.job-intro--text-box {
		position: relative;
		z-index: 2;
		text-align: right;

		.btn {
			padding: 0.4em 1.6em;
			font-size: rem(16px);
			color: white;
			background-color: $secondary-color;
			font-weight: 900;
			border: none;
			&:hover {
				background-color: darken($secondary-color, 5);
			}
		}
	}
	.breadcrumb-holder {
		position: absolute;
		bottom: 0;
		width: 100%;
		z-index: 2;
		.breadcrumb {
			background-color: transparent;
			margin: 0;
			// position: absolute;
			// bottom: 0;
			// right: 0;
			.breadcrumb-item {
				font-size: rem(12px);
				color: white;
				& + .breadcrumb-item {
					padding-right: rem(8px);
					&::before {
						padding-right: 0;
						padding-left: rem(8px);
						display: inline-block;
						content: '/';
						color: white;
					}
				}
			}
		}
	}
	.job-intro--social-links {
		display: flex;
		justify-content: center;
		flex-direction: column;
		position: absolute;
		left: 0;
		top: 50px;
		bottom: 100px;
		z-index: 20;
		padding: rem(32px);
		opacity: 0.7;
		a + a {
			margin-top: rem(16px);
		}
	}
}
