.hiring-step {
	margin-top: rem(24px);
	.hiring-step-item {
		.hiring-step-item--img {
			box-shadow: $global-shadow;
			background-color: white;
			border-radius: 50%;
			position: relative;
			width: rem(120px);
			height: rem(120px);
			margin: auto;
			margin-bottom: rem(24px);
			img {
				width: rem(70px);
				height: rem(70px);
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
			}
		}
		.hiring-step--title {
			font-weight: 900;
			font-stretch: normal;
			font-style: normal;
			line-height: 1.56;
			letter-spacing: normal;
			color: $text-color-blue;
		}
	}
}
