.section-header {
	.section-header--title {
		font-size: rem(18px);
		text-align: center;
		font-weight: 900;
		color: $primary-color;
		position: relative;
		display: inline-block;
		// background-color: white;
		z-index: 10;
		// padding: 0 rem(30px);
		&::after {
			content: '';
			position: absolute;
			right: -65px;
			top: 50%;
			transform: translateY(-50%);
			height: rem(3px);
			width: rem(60px);
			background-color: $image-bg;
		}
		&::before {
			content: '';
			position: absolute;
			left: -65px;
			top: 50%;
			transform: translateY(-50%);
			height: rem(3px);
			width: rem(60px);
			background-color: $image-bg;
		}
	}
}
