.item-row {
	display: flex;
	justify-content: space-between;
	align-items: center;
	margin: rem(12px 0);
	padding: rem(12px);
	border: rem(1px) solid $gray-500;
	border-radius: rem(8px);
	font-weight: 500;
	color: $text-color-secondary;
	transition: opacity 200ms ease-in-out;
	&.is-deactivate {
		opacity: 0.5;
	}
}
