.link-page {
	.link-page--result {
		padding-top: rem(24px);
		border-top: rem(1px) solid $gray-400;
		.link-page--result--title {
			font-size: rem(16px);
			font-stretch: normal;
			font-style: normal;
			letter-spacing: normal;
			text-align: right;
			color: $gray-500;
			width: rem(200px);
			display: inline-block;
		}
		.link-page--result--link {
			font-size: 12px;
			font-stretch: normal;
			font-style: normal;
			line-height: 1.69;
			letter-spacing: normal;
			color: $blue-dark;
			direction: ltr;
			.link-page--result--copy {
				background-color: $gray-400;
				color: $gray-600;
				border-radius: rem(16px);
				margin-left: rem(8px);
				font-size: rem(9px);
				height: rem(24px);
				line-height: rem(12px);
				svg {
					margin-right: rem(4px);
					width: rem(12px);
					height: rem(12px);
				}
			}
		}
	}
	.btn {
		&.isLoading {
			&::after {
				border-width: rem(4px);
				width: rem(25px);
				height: rem(25px);
				margin: -12px;
			}
		}
	}
}
