.jobBox {
	border: solid rem(1px) $light-gray;
	box-shadow: $jobBox-shadow;
	border-radius: rem(8px);
	.quill .ql-container {
		min-height: auto !important;
	}

	.accordion {
		border-radius: rem(8px);
	}
	.jobBox--header {
		transition: background-color 200ms ease-in-out,
			border-bottom-left-radius 200ms ease-in-out,
			border-bottom-right-radius 200ms ease-in-out;
		border-radius: rem(8px);
		.jobBox--header--button {
			transition: color 200ms ease-in-out;
			width: 100%;
			color: $text-color;
			line-height: rem(40px);
			text-decoration: none;
			&:focus,
			:hover {
				outline: none;
				box-shadow: none;
			}
			.jobBox--header--button-icon {
				width: rem(16px);
				margin-left: rem(8px);
				&.is-rotate {
					svg {
						transition: transform 200ms ease-in-out;
						transform: rotate(0deg);
					}
				}
				&.is-immediately {
					color: $danger;
					svg {
						width: rem(18px);
					}
				}
			}
			.jobBox--header--button-title {
				font-size: rem(14px);
				margin-bottom: 0;
				line-height: rem(40px);
				float: right;
				&.is-immediately {
					color: $danger;
				}
			}
		}
		@media (max-width: 540px) {
			.jobBox--header--button {
				display: flex;
				align-items: center;
				justify-content: space-between;
				.jobBox--header--button-icon,
				.jobBox--header--button-title {
					line-height: rem(30px);
				}
			}
		}
	}
	.collapse.show {
		border-radius: rem(8px);
	}
	.btn {
		box-shadow: none;
	}
	.job-details {
		color: $text-color;
		.job-details--title {
			svg {
				color: $text-color-secondary;
				width: rem(14px);
				margin-left: rem(4px);
			}
		}
	}
	&.isOpen {
		.jobBox--header {
			background-color: $job-header-color;
			border-bottom-left-radius: 0;
			border-bottom-right-radius: 0;
			.jobBox--header--button {
				color: white;
				line-height: rem(40px);
				.is-rotate {
					svg {
						transform: rotate(-90deg);
					}
				}
				.is-immediately {
					color: white !important;
				}
			}
		}
	}
}
