.admin-header {
	// background-color: $gray-200;
	box-shadow: $global-shadow;
	border-bottom: rem(1px) solid $gray-300;
	background-color: $bootstrap-light;
	// @media (max-width: 991px) {
	// 	padding-top: rem(34px);
	// }
	.navbar-nav {
		.navbar-nav--item {
			@media (max-width: 991.98px) {
				align-self: baseline;
			}
		}
	}
	.navbar-light .navbar-toggler-icon {
		background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg viewBox='0 0 30 30' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath stroke='rgba(0, 0, 0, 0.5)' stroke-width='2' stroke-linecap='round' stroke-miterlimit='10' d='M4 7h22M4 15h22M4 23h22'/%3E%3C/svg%3E") !important;
	}
	.navbar-toggler {
		padding: 0.25rem 0.75rem !important;
	}

	@media (max-width: 991px) {
		.navbar .navbar-collapse.show::before,
		.navbar .navbar-collapse.collapsing::before {
			opacity: 0 !important;
			display: none;
		}
		.navbar .navbar-collapse {
			height: unset;
			background-color: $bootstrap-light !important;
		}
	}
	.is-active {
		color: $text-color;
		font-weight: 700;
		border-bottom: rem(2px) solid $text-color;
	}

	.btn {
		background-color: transparent !important;
		border-color: transparent !important;
		&:hover,
		&:focus {
			outline: none !important;
			border-color: transparent !important;
			box-shadow: none !important;
		}
		&.is-hover-gray {
			&:hover {
				background-color: $gray-200 !important;
			}
		}
	}
	.header-logo {
		text-align: left;
		img {
			width: 70%;
		}
	}
}
