.switch-btn {
	line-height: 0;
	font-size: 0;
	input[type='checkbox'] {
		height: 0;
		width: 0;
		visibility: hidden;
	}

	label {
		cursor: pointer;
		text-indent: -9999px;
		width: 40px;
		height: 20px;
		background: $gray-400;
		display: block;
		border-radius: 100px;
		position: relative;
		margin: 0;
	}

	label:after {
		content: '';
		position: absolute;
		top: 1px;
		left: 2px;
		width: 18px;
		height: 18px;
		background: white;
		border-radius: 90px;
		transition: 300ms;
	}

	input:checked + label {
		background: $active;
	}

	input:checked + label:after {
		left: 98%;
		transform: translateX(-100%);
	}

	label:active:after {
		width: 30px;
	}
	&.isLoading {
		min-height: auto;
		&:after {
			border: 3px solid $primary-color;
			border-top: 3px solid $text-color-light;
			border-width: rem(3px);
			width: rem(20px);
			height: rem(20px);
			top: rem(20px);
			left: rem(30px);
		}
		&:before {
			border-radius: rem(20px);
		}
	}
}
