.job-app {
	.list-no-styled {
		list-style: none;
	}
	.overflow-hidden {
		overflow: hidden;
	}
	@keyframes spin {
		0% {
			transform: rotate(0deg);
		}
		100% {
			transform: rotate(360deg);
		}
	}
	.is-rounded-8 {
		border-radius: rem(8px);
	}
	.is-rounded-16 {
		border-radius: rem(16px);
	}
	.is-white {
		color: white;
	}
	.is-primary-color {
		color: $primary-color;
	}

	.is-danger {
		color: $danger;
	}
	.is-danger-bg {
		background-color: $danger;
	}
	.is-hove-white {
		&:hover {
			color: white;
		}
	}

	.is-active-color {
		color: $active;
	}
	.is-dark-blue-color {
		color: $text-color-blue;
	}

	.is-bg-white {
		background-color: white;
	}
	.is-bg-gray-600 {
		background-color: $gray-600;
	}

	.is-color-text-color-secondary {
		color: $text-color-secondary;
	}
	.is-primary-bg {
		background-color: $primary-color;
	}
	.min-height-isLoading {
		min-height: rem(66px);
	}

	.font-10 {
		font-size: rem(10px);
	}
	.font-10-important {
		font-size: rem(10px) !important;
	}
	.font-12 {
		font-size: rem(12px);
	}
	.font-14 {
		font-size: rem(14px);
	}
	.font-16 {
		font-size: rem(16px);
	}
	.font-18 {
		font-size: rem(18px);
	}
	.font-20 {
		font-size: rem(20px);
	}
	.font-24 {
		font-size: rem(24px);
	}
	.font-100 {
		font-size: rem(100px);
	}
	.is-cursor-pointer {
		cursor: pointer;
	}

	.form-file-uploader {
		height: rem(32px);
		border: 1px solid $form-border;
		border-radius: rem(4px) !important;
		border-width: rem(1px) !important;
	}
	.site-header-margin {
		margin-top: rem(34px);
	}
	.has-margin-header {
		margin-top: rem(76px);
	}
	.is-ltr {
		direction: ltr;
	}
	.en-font-family {
		font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
	}
}

.btn {
	&.btn-dark-blue {
		background-color: $text-color-blue !important;
		color: white !important;
		&:hover {
			background-color: darken($text-color-blue, 5) !important;
		}
	}
}
