.radio-text {
	label {
		float: right;
		margin: 0;
		margin-left: rem(8px);
	}

	span {
		font-size: rem(14px);
		font-stretch: normal;
		font-style: normal;
		letter-spacing: normal;
		cursor: pointer;
		transition: all 2ms ease;
		border: rem(1px) solid $gray-500;
		border-radius: rem(4px);
		color: $gray-500;
		padding: rem(4px 8px);
		line-height: rem(38px);
		height: rem(38px);
	}
	input {
		display: none !important;
	}
	input:checked + span {
		color: $active;
		border-color: $active;
	}
	&.is-alternative {
		label {
			float: none;
		}
		input:checked + span {
			color: white;
			background-color: $active;
			border-color: $active;
		}
	}
	&.second-style {
		label:nth-child(n-1) {
			position: relative;
			padding-left: rem(18px);
			float: none;
		}
		span {
			border: 0;
			padding: rem(2px 4px);
		}
		input:checked + span:before,
		input:not(:checked) + span:before {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: rem(20px);
			height: rem(20px);
			border: rem(2px) solid $form-border;
			transform: translateY(-50%);
			border-radius: 100%;
			background: white;
		}
		input:checked + span:before {
			border: rem(2px) solid $primary-color;
		}
		input:checked + span:after,
		input:not(:checked) + span:after {
			content: '';
			width: rem(12px);
			height: rem(12px);
			background: $primary-color;
			position: absolute;
			top: 50%;
			left: rem(4px);
			border-radius: 100%;
			transition: all 200ms ease;
			transform: translateY(-50%);
		}
		input:not(:checked) + span:after {
			opacity: 0;
			// transform: scale(0) translateY(-50%);
		}
		input:checked + span:after {
			opacity: 1;
			// transform: scale(1) translateY(-50%);
		}
		&.has-error {
			input:not(:checked) + span:before {
				border: rem(2px) solid $danger;
			}
		}
		// .error-box {
		// 	top: rem(20px);
		// }
	}
}
