.title-page {
	.job-title-tabs {
		background-color: white;
		border: rem(1px) solid $gray-400;
		padding: 0 !important;
		border-radius: rem(4px);
		.nav-item {
			.nav-link {
				position: relative;
				font-size: rem(14px);
				padding: 16px 12px;
				border-radius: 0;
				display: flex;
				justify-content: space-between;
				&::after {
					content: '';
					position: absolute;
					height: rem(1px);
					background-color: $gray-400;
					bottom: rem(-1px);
					right: rem(8px);
					left: rem(8px);
				}
			}
			&:first-child {
				.active {
					border-top-right-radius: rem(4px);
					border-top-left-radius: rem(4px);
				}
			}
			&:last-child {
				.active {
					border-bottom-right-radius: rem(4px);
					border-bottom-left-radius: rem(4px);
				}
				.nav-link {
					&::after {
						display: none;
					}
				}
			}
			.active {
				background-color: $primary-color;
			}
		}
	}
	.tab-content {
		background-color: white;
		padding: rem(12px);
		border-radius: rem(4px);
		border: rem(1px) solid $gray-400;
	}
}
