.isLoading {
	position: relative;

	&::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
		bottom: 0;
		background-color: black;
		opacity: 0.5;
		z-index: $base-z-index;
		border-radius: rem(8px);
	}

	&::after {
		content: '';
		border: 6px solid $primary-color;
		border-top: 6px solid $text-color-light;
		border-radius: 50%;
		width: 40px;
		height: 40px;
		animation: spin 2000ms linear infinite;
		position: absolute;
		top: 50%;
		left: 50%;
		z-index: 1050;
		margin: -20px;
	}
}
