.comment-box {
	padding: rem(46px 24px);
	border-radius: rem(8px);
	box-shadow: $jobBox-shadow;
	margin: rem(4px);
	border: solid rem(1px) $light-gray;
	background-color: white;
	.comment-box--comment {
		font-size: rem(16px);
		font-weight: 500;
		font-stretch: normal;
		font-style: normal;
		line-height: 1.56;
		letter-spacing: normal;
		text-align: right;
		color: text-color-secondary;
	}
	.comment-box--user {
		font-size: rem(16px);
		font-weight: 500;
		font-stretch: normal;
		font-style: normal;
		line-height: 1.56;
		letter-spacing: normal;
		color: $primary-color;
		display: block;
		margin: auto;
	}
}
