///////////////////////////////////
/////////////  color  /////////////
///////////////////////////////////
$primary-color: #03aaa6;
$secondary-color: #009ca9;
$panel-bg: #f3f6f9;
$primary-color-darken: darken($primary-color, 0.5);
$light-primary-color: #70d3d9;
$green: #00aa96;
$active: #1bbf89;
$deactivate: #979797;
$light-gray: #e6e8ed;
$text-color: #2c2f33;
$text-color-blue: #004e6e;
$text-color-light: #848e99;
$text-color-secondary: #585f66;
$form-border: #ced4da;
$job-header-color: #0bb8ae;
$bootstrap-light: #f8f9fa;
$disabled: #f0f2f4;

$white: #fff !default;
$gray-100: #f8f9fa !default;
$gray-200: #e9ecef !default;
$gray-300: #dee2e6 !default;
$gray-400: #ced4da !default;
$gray-500: #adb5bd !default;
$gray-600: #6c757d !default;
$gray-700: #495057 !default;
$gray-800: #343a40 !default;
$gray-900: #212529 !default;
$black: #000 !default;

$badge-color: #596a7a;
$warning: #ffab12;
$danger: #ff6969;
$input-placeholder: #b5b7bb;
$image-bg: #dadada;
$blue-dark: #004e6e;

$input-error-bg: rgb($danger, 0.1);

///////////////////////////////////
/////////////  size  //////////////
///////////////////////////////////
$header-height: 180px;
$header-icon-width: 78px;
$footer-hight: 60px;
$footer-btn-width: 40px;
$field-margin: 48px;
$input-width: 100%;
$input-height: 40px;
$global-border: 4px;

////////////////////////////////////
/////////////  shadow  /////////////
////////////////////////////////////
$global-shadow: 0 0 rem(4px) 0 rgb(0 0 0 / 0.15);
$jobBox-shadow: 0 0 rem(20px) 0 rgb(175 190 204 / 0.4);
$input-shadow: 0 0 rem(10px) 0 rgb(0 0 0 / 0.15);
$form-shadow: 0 0 0 0.2rem rgb(0 170 150 / 0.25);
$form-shadow-error: 0 0 0 0.2rem rgb($danger, 0.5);
$primary-shadow: 0 0 0 0.2rem rgb($primary-color-darken, 0.5);
////////////////////////////////////
/////////////  gradient  /////////////
////////////////////////////////////
$benefit-remote-gr: linear-gradient(
	90deg,
	rgb(0 127 180 / 1) 0%,
	rgb(0 65 129 / 1) 100%
);
$hiring-step: linear-gradient(to bottom, #e8f7f7 21%, rgb(232 247 247 / 0));
////////////////////////////////////
////////////  overlay  /////////////
////////////////////////////////////
$more-info-overlay: rgb(0 0 0 / 0.2);

////////////////////////////////////
////////////  others  /////////////
////////////////////////////////////
// $font-family: 'IRANSansFa';
$font-family: IRANSans;
$base-z-index: 1000;
