.input-form-hook {
	font-size: 0;
	width: 100%;
	position: relative;
	margin-bottom: 20px;
	&.px-0 {
		.input-form-hook--icon {
			right: 12px;
		}
	}
	.input-form-hook--label {
		display: block;
		font-size: rem(12px);
	}
	.input-form-hook--input {
		font-size: 12px;
		&.input-form-hook--input--not-valid {
			background-color: $input-error-bg !important;
			border: 1px solid $danger !important;
			&:focus {
				box-shadow: $form-shadow-error !important;
				border-color: $danger !important;
			}
		}
		&::placeholder {
			opacity: 0.4;
		}
		&.is-small-placeholder {
			&::placeholder {
				font-size: 10px;
			}
		}
	}
	.input-form-hook--icon {
		font-size: 20px;
		position: absolute;
		top: 50%;
		right: 12px;
		transform: translateY(-50%);
	}
	.input-form-hook--password-icon {
		position: absolute;
		top: 50%;
		left: 20px;
		color: $text-color-secondary;
		cursor: pointer;
	}
	.input-form-hook--error {
		right: rem(12px);
		position: absolute;
		font-size: 10px;
		color: $danger;
	}
	&.radio-text {
		.input-form-hook--input {
			&.input-form-hook--input--not-valid + span {
				&::before {
					background-color: $input-error-bg;
					border-color: $danger;
					color: $danger;
					&:focus {
						box-shadow: $form-shadow-error;
						border-color: $danger;
					}
				}
			}
		}
	}
	&.is-textarea {
		.input-form-hook--input {
			height: auto !important;
		}
		// .input-form-hook--error {
		// 	bottom: 12px;
		// }
	}
	&.panel--input {
		.input-form-hook--label {
			line-height: rem(38px) !important;
		}
		.input-form-hook--error {
			top: rem(38px);
			right: rem(150px);
		}
		&[class*='col-'] {
			.input-form-hook--error {
				right: rem(160px);
			}
		}
		&.is-large {
			label:first-of-type {
				width: rem(200px);
			}
			input,
			select {
				width: calc(100% - 200px);
			}
			.input-form-hook--error {
				right: rem(200px);
			}
			&[class*='col-'] {
				.input-form-hook--error {
					right: rem(210px);
				}
			}
		}
	}

	.react-calendar-datetime-picker .input-picker {
		.input-form-hook--input {
			width: 100%;
			font-size: 12px;
			border: 2px solid $light-gray;
		}
	}
}
