.home-page {
	min-height: 100vh;
	.is-padding-top {
		padding-top: rem(16px);
		// @media (max-width: 991px) {
		// 	padding-top: rem(44px);
		// }
	}
	.home-media {
		background-color: $light-gray;
	}
	.home-benefits {
		.home-benefits--remote {
			margin: auto;
			width: 80%;
			background-image: $benefit-remote-gr;
			display: flex;
			justify-content: center;
			align-items: center;
			padding: rem(12px);
			border-radius: rem(8px);
			.home-benefits--img {
				margin-left: rem(18px);
			}
		}
		.home-benefits--img {
			border-radius: 50%;
			background-color: $light-gray;
			padding: rem(30px);
		}
		.home-benefits--title {
			color: $text-color-blue;
		}
	}
	.job-list {
		&.isLoading {
			&:before {
				border-radius: rem(8px);
			}
		}
	}

	.home-comment {
		position: relative;
		.home-comment-img {
			position: absolute;
			top: rem(-40px);
			right: rem(-135px);
			@media (max-width: 767px) {
				display: none;
			}
		}
	}
	.home-hiring-step {
		padding: rem(30px 0);
		background: $hiring-step;
	}
}
