	.accordion {
		border-radius: rem(8px);
	}
	.commonQuestionBox--header {
		.commonQuestionBox--header--button {
			width: 100%;
			&:focus {
				outline: none;
				box-shadow: none;
			}
			.commonQuestionBox--header--button-icon {
				width: rem(24px);
				color: $primary-color;
				float: left;
			}
			.commonQuestionBox--header--button-title--helper {
				position: relative;
				width: calc(100% - 24px - 8px);
				float: right;
				&:after {
					content: '';
					width: 100%;
					display: block;
					position: absolute;
					top: 50%;
					transform: translateY(-50%);
					left: rem(30px);
					right: 0;
					height: rem(2px);
					background-color: $image-bg;
					z-index: 1;
				}
				.commonQuestionBox--header--button-title {
					position: relative;
					z-index: 2;
					background-color: white;
					font-size: rem(18px);
					font-weight: 700;
					font-stretch: normal;
					font-style: normal;
					line-height: 1.5;
					letter-spacing: normal;
					text-align: right;
					color: $primary-color;
					float: right;
					padding-left: rem(12px);
				}
			}
		}
	}
	.collapse.show {
		border-radius: rem(8px);
	}
}
